<template>
  <div class="recruimentModelBox">
    <div class="recruimentModelList">
      <div class="recruimentModel-title">
        <h1>招聘信息</h1>
        <div class="searchBox">
          <el-input placeholder="请输入搜索关键字" v-model="searchTip" class="input-with-select">
            <el-select v-model="searchId" slot="prepend" placeholder="请选择">
              <el-option label="岗位名称" value="1"></el-option>
              <el-option label="公司名称" value="2"></el-option>
            </el-select>
            <el-button slot="append" icon="el-icon-search" @click="searchOut"></el-button>
          </el-input>
        </div>
      </div>
      <div class="recurimentList">
        <div class="table-container">
          <div class="table-head">
            <div class="table-item name">职位名称</div>
            <div class="table-item companyName">公司名称</div>
            <div class="table-item xinzi">职位月薪</div>
            <div class="table-item address">工作地点</div>
          </div>
          <div class="table-body">
            <div class="table-body-item" v-for="(item,index) in dataList" :key="index">
              <div class="body">
                <div class="info">
                  <div class="head">
                    <div class="table-item name">
                      <a
                        :href="item.deliveryUrl"
                        target="view_window"
                        title="单击跳转投递页面"
                      >{{item.recruitmentPost}}</a>
                    </div>
                    <div class="table-item companyName">{{item.corporateName}}</div>
                    <div class="table-item xinzi">
                      <span>
                        <i style="color: rgb(247, 100, 100);">{{item.salary}}元</i>/月
                      </span>
                    </div>
                    <div class="table-item address">{{item.workPlace}}</div>
                  </div>
                  <div class="infoText">
                    <div class="item">招聘地点:{{item.recruitingAddress}}</div>
                    <div class="item">招聘人数:{{item.recruitingNumbers}}</div>
                    <div
                      class="item"
                    >招聘时间:{{ item.recruitingTime.split(",")[0]}} ~ {{ item.recruitingTime.split(",")[1]}}</div>
                    <div class="item">发布者:{{item.counselorNo}}</div>
                  </div>
                  <div class="content">
                    <div class="text" :title="item.jobRequirements">
                      <span style="color: rgb(0, 0, 0);">岗位职责:</span>
                      <span>{{item.jobRequirements}}</span>
                    </div>
                  </div>
                  <div class="content">
                    <div class="text" :title="item.companyProfile">
                      <span style="color: rgb(0, 0, 0);">公司简介:</span>
                      <span>{{item.companyProfile}}</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { interRecruitMentInfoAll } from "../../../../../utils/employment.url";
export default {
  data() {
    return {
      searchTip: "",
      searchId: "",
      recruitmentList: [],
      dataList:[]
    };
  },
  methods: {
    //获取招聘信息列表
    getRecruitMentInfo() {
      interRecruitMentInfoAll({}, "get")
        .then(res => {
          if (res.status == 200) {
            this.recruitmentList = res.data;
            this.dataList = res.data;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    searchOut() {
      if(this.searchId == 1){
        this.dataList = this.recruitmentList.filter(
        data =>
          !this.searchTip || data.recruitmentPost.toLowerCase().includes(this.searchTip.toLowerCase())
      );
      }else{
        this.dataList =  this.recruitmentList.filter(
        data =>
          !this.searchTip || data.corporateName.toLowerCase().includes(this.searchTip.toLowerCase())
      );
      }
      
    }
  },
  created() {
    this.getRecruitMentInfo();
  }
};
</script>
<style lang="scss" scoped>
.recruimentModelBox {
  width: 1200px;
  margin: 10px auto;
  .recruimentModelList {
    .recruimentModel-title {
      width: 100%;
      border-bottom: 1px solid #dedede;
      h1 {
        color: #51a1ff;
        font-size: 18px;
        font-weight: 500;
        width: 94px;
        padding: 16px 0;
        border-bottom: 3px solid #51a1ff;
      }
      h1,
      .searchBox {
        display: inline-block;
      }
      .searchBox {
        float: right;
        margin-right: 20px;
        margin-top: 10px;
      }
    }
    .recurimentList {
      .table-container {
        .table-head {
          display: flex;
          height: 48px;
          line-height: 48px;
          padding-left: 15px;
          background-color: #f5f5f5;
          margin: 15px 0;
          font-size: 16px;
          font-weight: 300;
          .table-item {
            width: 250px;
            padding-right: 20px;
            // cursor: pointer;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
          }
        }
        .table-body {
          .table-body-item {
            padding: 10px 15px;
            background-color: #fff;
            border: 1px solid #51a1ff;
            margin-bottom: 12px;
            border-left: 5px solid #51a1ff;
            .body {
              display: -webkit-box;
              display: -ms-flexbox;
              display: flex;
              -webkit-box-align: center;
              -ms-flex-align: center;
              align-items: center;
              .info {
                color: #888;
                font-size: 12px;
                font-weight: 300;
                margin: 10px 0;
                letter-spacing: 1px;
                .head {
                  margin-bottom: 10px;
                  display: block;
                  .table-item {
                    display: inline-block;
                    width: 240px;
                    padding-right: 20px;
                    // cursor: pointer;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    color: #51a1ff;
                    font-size: 18px;
                    font-weight: 500;
                  }
                  .name {
                    font-size: 18px;
                    font-weight: 500;
                  }
                  .companyName {
                    // width: 260px;
                    padding-right: 20px;
                    color: #000;
                    font-size: 14px;
                  }
                  .xinzi {
                    // width: 260px;
                    font-size: 14px;
                  }
                  .address {
                    color: #888;
                    // width: 200px;
                    font-size: 14px;
                  }
                }
                .infoText {
                  width: 100%;
                  margin-bottom: 10px;
                  .item {
                    display: inline-block;
                    margin-left: 48px;
                  }
                  :nth-of-type(1) {
                    margin-left: 0px;
                  }
                }
                .content {
                  margin-top: 10px;
                  color: #888;
                  font-size: 12px;
                  font-weight: 300;
                  .text {
                    text-align: justify;
                    color: #888;
                    font-size: 14px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: 3;
                    -webkit-box-orient: vertical;
                    cursor: pointer;
                    span {
                      line-height: 22px;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>
<style>
.el-select .el-input {
  width: 130px;
}
.input-with-select .el-input-group__prepend {
  background-color: #fff;
}
</style>